@use "sass:math";
.talk {
  position: absolute;
  z-index: 500;
  width: 25%;
  background: #fff;
  border-radius: 1em;
  opacity: 0;
  &.talking {
    transition: opacity 0.4s ease;
    opacity: 1;
  }
  &__box {
    width: 100%;
    height: 100%;
    position: relative;
    padding: 0.75em;
  }
  &__text {
    p {
      margin: 0;
    }
  }

  &__arrow {
    position: absolute;
    width: 1em;
    height: 1em;
    background: #fff;

    clip-path: polygon(50% 100%, 0 0, 100% 0);
  }
  &.none &__arrow {
    display: none;
  }
  &.bl &__arrow {
    top: 91%;
    left: 5%;
  }
  &.br &__arrow {
    top: 99%;
    right: 5%;
  }
  &.bc &__arrow {
    top: 98%;
    left: 50%;
    transform: translate(-50%, 0%);
  }
  &.cl &__arrow {
    top: 50%;
    left: 0%;
    transform: translate(-100%, -50%) rotate(90deg);
  }
  &.cr &__arrow {
    top: 50%;
    left: 100%;
    transform: translate(0, -50%) rotate(-90deg);
  }
  &.tl &__arrow {
    transform: rotate(135deg);
    top: -8%;
    left: -3%;
  }
  &.tc &__arrow {
    top: 0%;
    left: 50%;
    transform: translate(-50%, -100%) rotate(180deg);
  }
}
