.phoneRotateWarn.show {
  .phoneRotateWarn__container{
    opacity: 1;
  }
}

.phoneRotateWarn {
  display: flex;
  position: fixed;
  z-index: 99999999;
  width: 100%;
  height: 100%;
  background: #000;
  align-items: center;

  &__container {
    opacity: 0;
    width: 85%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;

  }

  &__icon {
    height: 12em;
    width: 10em;

    img {
      object-fit: contain !important;
    }
  }

  &__text {
    max-width: 640px;
    text-align: center;
    font-size: 2em;
  }

  &__text p {
    line-height: 130%;
    font-family: $Muller;
    color: #fff;
    margin-bottom: 0;
  }
}
