@import "~normalize.css";
@import "support/vars";
@import "support/mixin";
@import "fonts";
@import "mainBlocks/preloader";
@import "mainBlocks/scene";
@import "mainBlocks/spines";
@import "components/onSpine";
@import "components/talk";
@import "components/next-btn";
@import "mainBlocks/warn";
@import "mainBlocks/play";
@import "animation/fade";
@import "animation/anim";
@import "components/cave";
@import "components/interface";
@import "components/settings";
