@import "mainBlocks/phoneRotate";

// main scss
* {
  font-family: "Roboto", sans-serif;
  box-sizing: border-box;
  user-select: none;
}

*.willShake {
  transform: scale(1.05);
}

*.shaking {
  transform: translate(0, 0);
  animation: shakingNoScale 0.72s linear 0s forwards infinite;
}

html,
body {
  background-color: #000;
  width: 100%;
  height: 100%;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

.preloadImg {
  width: 0;
  height: 0;
}

.img {
  position: relative;
  width: 100%;
  height: 100%;

  img {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    top: 0;
    left: 0;
    object-position: center;
  }
}

main {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
}

button {
  margin: 0;
  padding: 0;
  border: none;
}


.Drake1,
.big-silly-after,
.Drake-changeImg {
  transform: scale(-1, 1);
}

.statue,
.silly-after-statue,
.statue-trap {
  transform: translate(-50%, -100%);
}

.statue-trap2,
.statue-trap3 {
  transform: translate(-50%, -86%);
}

.smoke-img {
  transform: translate(-43%, 0);
}

.helena-stand,
.drake-stand,
.drake-scare {
  transform: translate(0%, -100%);
}

.helena-trapB {
  transform: translate(-42.5%, -100%);
}

.helena-trapF {
  transform: translate(-30.5%, -100%) !important;
}

.lizard-big-bg {
  transform: scale(2) translate(-10%, 0);
}



@keyframes shakingNoScale {
  0% {
    transform: translate(0, 0);
  }
  25% {
    transform: translate(0, 0.5%);
  }
  50% {
    transform: translate(0, 0);
  }
  75% {
    transform: translate(0, -0.5%);
  }
  100% {
    transform: translate(0, 0%);
  }
}