#scene8,
#scene9 {
  transform: scale(1.05);
}

#scene9.anim {
  animation: shaking 0.72s linear 5s 7 forwards;
}

.Helena-sit-anim.anim {
  @include adaptive_value('font-size', 30, 10);
  transform: translate(0, 1em);
  transition: transform 5s linear 5s, opacity 0s linear 14s;
  opacity: 0;
}

.stone-anim.anim {
  @include adaptive_value('font-size', 30, 10);
  transform: translate(0, 1em);
  transition: transform 5s linear 5s;
}

.Helena-sit-animated,
.stone-animated {
  @include adaptive_value('font-size', 30, 10);
  transform: translate(0, 1em);

}

.door-anime.anim {
  animation: animDoor 7s linear 10s forwards;
}

@keyframes anim {
  0% {
    @include adaptive_value('font-size', 30, 15);
    transform: translate(0, 0);
  }
  100% {
    @include adaptive_value('font-size', 30, 15);
    transform: translate(0, 1em);
  }
}

@keyframes shaking {
  0% {
    transform: scale(1.05) translate(0, 0);
  }
  25% {
    transform: scale(1.05) translate(0, 0.5%);
  }
  50% {
    transform: scale(1.05) translate(0, 0);
  }
  75% {
    transform: scale(1.05) translate(0, -0.5%);
  }
  100% {
    transform: scale(1.05) translate(0, 0%);
  }
}

@keyframes animDoor {
  0% {
    transform: translate(0%, 0%);
  }
  100% {
    transform: translate(100%, 0%);
  }
}
