.scene {
  width: 100%;
  height: 100%;
  position: absolute;
  overflow: hidden;
  background: #000;
  
  &__background,
  &__characters,
  &__spine {
    position: absolute;
    width: 100%;
    height: 100%;
  }
}

.background-scene,
.characters-scene,
.spine-scene {
  &__item {
    position: absolute;
  }
}
