.helena-trapB,
.statueFace,
.statueOpenFace,
.helena-trapF {
	transition: opacity 0s linear 2s;
}
.smoke-img {
	opacity: 0;
	transition: opacity 4s linear 2.5s;
}
//
.helena-trapB,
.statueFace {
	opacity: 1;
}
.helena-trapF,
.statueOpenFace {
	opacity: 0;
}

//
.helena-trapB.anim,
.statueFace.anim {
	opacity: 0;
}
.helena-trapF.anim,
.statueOpenFace.anim,
.smoke-img.anim {
	opacity: 1;
}
