.gameInterface {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 301;
  display: none;

  &.show {
    display: block;
  }

  &__container {
    width: 100%;
    height: 100%;
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include adaptive_value("padding", 60, 10);
  }

  &__btns {
    height: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    @include adaptive_value("font-size", 32, 8);
    gap: 0.6em;

    button:last-child {
      margin-top: 1em;
    }

    button.active:not(:disabled) {
      box-shadow: 0 0 0.3125em 0.125em $pink;

      &:hover::before {
        //transform: translate(-50%, -50%) scale(1.1);
        width: 98%;
        height: 98%;
      }
    }
  }

  &__progress {
    @include adaptive_value("width", 80, 40);
    height: 70%;
    border-radius: 10px;
    position: relative;
    overflow: hidden;
  }
}

.progress-gameInterface {

  &__progress.reset {
    transition: height 0s linear;
  }

  &__border {
    width: 100%;
    height: 100%;
    border-radius: 10px;
    border: 5px solid $pink;
    background: #000;
  }

  &__progress {
    position: absolute;
    width: 100%;
    background: $pink;
    transition: height 0.4s linear;
    bottom: 0;
    height: 0%;
    border-radius: 20px;
  }
}
