#onSpine {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 202;

  .onSpine__box {
    position: relative;
    width: 100%;
    height: 100%;

    .background-scene__item, .ligth1 {
      display: none;

      &.show {
        display: block;
      }
    }

  }

}